html,
body {
  height: 100%;
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: -0.04em;
  color: #011d47;
  background-color: #fff;
  cursor: default;

  @include m($md) {
    font-size: 17px;
    line-height: 24px;
  }
}

.main {
  flex-grow: 1;
}

.container {
  margin: 0 auto;
  padding: 0 16px;
  width: 100%;
  max-width: 1232px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  position: relative;
}

h2,
h3,
h5,
h6 {
  font-weight: 600;
  letter-spacing: -0.06em;
}

h2 {
  font-size: 48px;
  line-height: 56px;

  @include m($xl) {
    font-size: 42px;
    line-height: 50px;
  }

  @include m($lg) {
    font-size: 36px;
    line-height: 48px;
  }
}

h3 {
  font-size: 34px;
  line-height: 46px;

  @include m($xl) {
    font-size: 32px;
    line-height: 40px;
  }

  @include m($lg) {
    font-size: 30px;
    line-height: 38px;
  }
}

h5 {
  font-size: 26px;
  line-height: 34px;
  letter-spacing: -0.04em;

  @include m($xl) {
    font-size: 26px;
    line-height: 34px;
  }

  @include m($lg) {
    font-size: 24px;
    line-height: 32px;
  }
}

h6 {
  font-size: 18px;
  line-height: 28px;
  letter-spacing: -0.04em;

  @include m($lg) {
    font-size: 20px;
    line-height: 30px;
  }
}

.rv-navigation {
  position: sticky;
  top: 110px;
  margin-right: 35px;
  padding-top: 68px;
  width: max-content;
  height: fit-content;

  span {
    display: block;
    margin-bottom: 12px;
    font-size: 14px;
    line-height: 24px;
    color: #394b65;
  }

  ul {
    li {
      line-height: 20px;

      &::after {
        display: none;
      }

      a {
        width: max-content;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: #011d47;
        transition: all 0.3s;

        &:hover {
          color: #09268d;
        }
      }

      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }

  @include m($lg) {
    top: 76px;
  }

  @include m($md) {
    position: relative;
    top: 0;
    margin-right: 0;
    width: 100%;
  }

  @include m($xl) {
    padding-top: 40px;
  }
}

.rv-button {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 33px;
  width: fit-content !important;
  min-width: fit-content !important;
  min-height: 62px;
  max-height: 62px;
  background-color: #011d47;
  transition: all 0.3s;

  p {
    height: 62px;
    font-weight: 600;
    font-size: 14px;
    line-height: 62px;
    text-transform: uppercase;
    color: #fff;
    transition: all 0.3s;
  }

  &__wrap {
    height: 62px;
    overflow: hidden;
  }

  &:focus,
  &:hover,
  &:active,
  &:target, {
    text-decoration: none;
    color: #fff;
  }

  &:hover {
    background-color: #09268d;

    p:first-of-type {
      transform: translateY(-100%);
    }

    p:last-of-type {
      transform: translateY(-100%);
    }
  }

  &:disabled {
    background-color: #abb9ca;

    p:first-of-type {
      transform: translateY(0);
    }

    p:last-of-type {
      transform: translateY(0);
    }
  }
}

.rv-secondary {
  border: 1px solid #abb9ca;
  background-color: #fff;

  p {
    color: #011d47;
  }

  &:hover {
    background-color: #f0f5ff;

    p {
      color: #09268d;
    }
  }

  &:disabled {
    background-color: #fff;

    p:first-of-type {
      transform: translateY(0);
    }

    p:last-of-type {
      transform: translateY(0);
    }

    p {
      color: #abb9ca;
    }
  }
}

.rv-ghost {
  padding: 0 24px;
  min-height: 70px;
  max-height: 70px;
  border: 1px solid #abb9ca;
  background-color: transparent;

  .rv-button__wrap {
    height: 70px;
  }

  p {
    height: 70px;
    line-height: 70px;
    color: #011d47;
  }

  &:hover {
    background-color: #f0f5ff;

    p:first-of-type {
      transform: translateY(-100%);
    }

    p:last-of-type {
      transform: translateY(-100%);
    }

    p {
      color: #09268d;
    }
  }

  &:disabled {
    background-color: #fff;

    p:first-of-type {
      transform: translateY(0);
    }

    p:last-of-type {
      transform: translateY(0);
    }

    p {
      color: #abb9ca;
    }
  }
}

.preview {
  position: relative;
  display: flex;
  align-items: center;
  margin-top: 88px;
  width: 100%;
  height: 400px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    width: 64%;
    height: 100%;
    background-color: rgba(#011d47, 0.72);

    @include m($md) {
      width: 100%;
    }
  }

  p {
    margin-bottom: 16px;
    max-width: 60%;
    color: #fff;

    @include m($lg) {
      display: none;
    }

    @include m($md) {
      max-width: 100%;
    }
  }

  h2 {
    max-width: 60%;
    color: #fff;

    @include m($md) {
      max-width: 100%;
    }
  }

  .container {
    z-index: 5;
  }

  @include m($lg) {
    margin-top: 56px;
  }
}

.big-preview {
  height: 600px;

  @include m($lg) {
    height: 400px;
  }
}

.qualify {
  margin-right: 24px;
  width: 100%;
  max-width: 384px;

  h6 {
    margin-bottom: 24px;
  }

  span {
    display: block;
    margin-bottom: 24px;
    font-size: 16px;
    line-height: 24px;
    color: #394b65;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  p:not(:first-of-type) {
    margin-top: 24px;
  }

  @include m(860) {
    margin-right: 0;
    margin-bottom: 40px;
    max-width: 100%;
  }
}

.back-button {
  display: flex;
  align-items: center;
  margin-bottom: 32px;
  height: 24px;

  svg {
    margin-right: 10px;
    width: 24px;
    height: 24px;
  }

  p {
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
    text-transform: uppercase;
    color: #011d47 !important;
  }

  @include m($lg) {
    margin-bottom: 24px;
  }
}

.other {
  padding: 120px 0;
  border-top: 1px solid #abb9ca;

  &__main {
    display: flex;
    justify-content: space-between;
  }

  .rv-ghost {
    margin-top: 10px;
  }

  h3 {
    @include rv-line;

    margin-bottom: 82px;

    @include m($lg) {
      margin-bottom: 64px;
    }
  }

  ul {
    display: grid;
    grid-gap: 0 24px;
    grid-template-columns: 1fr 1fr;

    li {
      a {
        width: 100%;
      }

      img {
        width: 100%;
        height: 382px;
        min-height: 285px;
        max-height: 382px;
        object-fit: cover;

        @include m($lg) {
          height: 300px;
        }

        @include m(830) {
          height: auto;
          min-height: 285px;
        }
      }

      &:first-child {
        .blog-item__descr {
          p {
            color: #011d47;
          }
        }
      }
    }

    .blog-item__descr {
      margin: 24px 0 16px;

      p {
        + p {
          &::after {
            background-color: #767e8b;
          }
        }
      }

      @include m($lg) {
        margin: 24px 0 12px;
      }
    }

    @include m(830) {
      grid-gap: 40px 0;
      grid-template-columns: 1fr;
    }
  }

  @include m($xl) {
    padding: 85px 0;
  }

  @include m($lg) {
    padding: 64px 0;
  }
}

.rv-popup {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 50;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  opacity: 0;
  background-color: rgba(#011d47, 0.4);
  transition: all 0.3s;

  &__wrapper {
    position: relative;
    padding: 56px;
    max-width: 551px;
    text-align: center;
    background-color: #fff;

    h5 {
      margin-bottom: 16px;
    }

    .rv-button {
      margin: 0 auto;
    }

    @include m($md) {
      padding: 46px 32px 24px;
    }
  }

  &__text {
    margin-bottom: 24px;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #767e8b;
  }

  &__close {
    position: absolute;
    right: 24px;
    top: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
    width: 24px;
    height: 24px;
    border: none;
    background-color: transparent;
  }

  @include m($md) {
    padding: 0 16px;
  }
}

.rv-popup--active {
  visibility: visible;
  opacity: 1;
}

.request__contact {
  position: relative;
  margin: 0 auto;
  max-width: 588px;

  .text-label-span {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    font-weight: 500;
    font-size: 12px;
    line-height: 20px;
    letter-spacing: -0.04em;
    color: #767e8b;
    transition: all 0.3s;
  }

  .career-input-block {
    display: grid;
    grid-gap: 32px 24px;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 56px;

    .text-label-span {
      top: 28px;
    }

    label {
      margin: 0;
      height: 69px;
    }

    .dad-active-input {
      .text-label-span {
        top: 0;
      }
    }

    .wpcf7-form-control-wrap {
      position: absolute;
      left: 0;
      top: 28px;
      width: 100%;
      height: 24px;
    }

    @include m($sm) {
      grid-gap: 24px 0;
      grid-template-columns: 1fr;
    }
  }

  .wpcf7-form-control-wrap {
    width: 100%;
    height: fit-content;

    textarea {
      position: absolute;
      top: 30px;
      margin: 0;
      padding: 0;
      width: 100%;
      height: 64px !important;
      max-height: 64px !important;
      outline: none;
      border-top: none;
      border-right: none;
      border-bottom: none;
      border-left: none;
      font-weight: 600;
      font-size: 14px;
      line-height: 24px;
      letter-spacing: -0.04em;
      color: #011d47;
      background-color: transparent;
      resize: none;
    }
  }

  .wpcf7-file {
    width: 100%;
    border: none;
    background-color: transparent;
  }

  p {
    width: 100%;
  }

  br {
    display: none;
  }

  span {
    display: block;
    width: 100%;
  }

  label {
    position: relative;
    display: block;
    margin-bottom: 32px;
    width: 100%;
    height: 100px;
    border-bottom: 1px solid #767e8b;

    input {
      padding: 0;
      width: 100%;
      height: 24px;
      outline: none;
      border: none;
      font-weight: 600;
      font-size: 14px;
      line-height: 24px;
      letter-spacing: -0.04em;
      color: #011d47;
      background-color: transparent;
    }
  }

  .wpcf7-submit {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 32px 0 0;
    padding: 0 33px;
    width: fit-content;
    height: 62px;
    min-width: fit-content;
    min-height: 62px;
    max-height: 62px;
    border: none;
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
    letter-spacing: -0.04em;
    text-transform: uppercase;
    color: #fff;
    background-color: #011d47;

    &:focus,
    &:hover,
    &:active,
    &:target, {
      text-decoration: none;
      color: #fff;
    }

    &:hover {
      background-color: #09268d;
    }

    &:disabled {
      background-color: #abb9ca;
    }
  }

  .wpcf7-list-item {
    display: block;
    margin: 0;

    label {
      position: relative;
      margin-bottom: 0;
      height: auto;
      border: none;

      input {
        position: absolute;
        left: 0;
        top: 0;
        margin: 0;
        padding: 0;
        width: 24px;
        height: 24px;
        border: 1px solid #011d47;
        border-radius: 50%;
        background-color: transparent;
        transition: all 0.3s;

        &:focus {
          background-color: #011d47;
        }
      }
    }

    .wpcf7-list-item-label {
      display: block;
      padding-left: 32px;
      font-weight: 500;
      font-size: 12px;
      line-height: 20px;
      letter-spacing: -0.04em;
      color: #394b65;
    }
  }
}

.button-search__active {
  svg {
    path {
      stroke: #abb9ca;
    }
  }
}

.strategy_under_img_1,
.strategy_under_img_2 {
  font-weight: 600;
  font-size: 46px;
  line-height: 56px;
  letter-spacing: -0.06em;

  @include m($xl) {
    font-size: 42px;
    line-height: 50px;
  }

  @include m($lg) {
    font-size: 36px;
    line-height: 48px;
  }
}

.strategy_under_img_1 {
  color: #09268d;
}

.strategy_under_img_2 {
  color: #ff535e;
}

.help {
  padding: 160px 0 94px;

  label {
    position: relative;
    margin: 0;
    width: 100%;
    height: 56px;

    button {
      position: absolute;
      left: 12px;
      top: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0;
      width: 30px;
      height: 30px;
      border: none;
      background-color: transparent;
      transform: translateY(-50%);

      svg {
        path {
          transition: all 0.3s;
        }
      }
    }

    input {
      padding: 16px 16px 16px 50px;
      width: 588px;
      height: 56px;
      outline: none;
      border: 1px solid #bed4ff;
      font-weight: 500;
      font-size: 14px;
      line-height: 24px;
      letter-spacing: -0.04em;
      color: #011d47;
      background-color: #f0f5ff;
      transition: all 0.3s;

      &::placeholder {
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        letter-spacing: -0.04em;
        color: #767e8b;
      }

      &:active,
      &:focus {
        background-color: #fff;
      }

      &:disabled {
        border: 1px solid #abb9ca;
        background-color: #fff;

        &::placeholder {
          color: #abb9ca;
        }

        input {
          color: #abb9ca;
        }
      }

      @include m($lg) {
        width: 448px;
      }

      @include m($md) {
        width: 100%;
      }
    }
  }

  form {
    width: 100%;
  }

  .category {
    h3 {
      margin: 32px 0;

      @include m($lg) {
        margin: 28px 0;
      }
    }

    h4 {
      margin-bottom: 24px;
      font-weight: 600;
      font-size: 24px;
      line-height: 32px;
      letter-spacing: -0.04em;
      color: #011d47;

      @include m($xl) {
        font-size: 22px;
        line-height: 30px;
      }

      @include m($lg) {
        margin-bottom: 18px;
        font-size: 18px;
        line-height: 28px;
      }
    }

    p {
      color: #394b65;

      &:not(:last-child) {
        margin-bottom: 28px;

        @include m($xl) {
          margin-bottom: 26px;
        }
      }

      @include m($xl) {
        font-size: 18px;
        line-height: 26px;
      }
    }
  }

  @include m($xl) {
    padding: 140px 0 59px;
  }

  @include m($lg) {
    padding: 120px 0 38px;
  }
}

.menu-footer-menu {
  .current_page_item {
    border-bottom: none !important;
    color: #011d47 !important;
  }
}

.wpcf7-not-valid-tip {
  position: absolute;
  top: 44px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: -0.04em;
  color: #ff535e;
}

.wpcf7-spinner {
  opacity: 0;
  background-color: transparent;
}

.form-file-wrap {
  position: relative;

  .wpcf7-not-valid-tip {
    top: -28px !important;
  }

  input {
    display: none !important;
  }
}

.form-file-label {
  position: relative;
  display: flex !important;
  align-items: center;
  justify-content: center;
  padding: 0 16px 0 48px;
  width: fit-content !important;
  height: 56px !important;
  border: 1px solid #abb9ca !important;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: -0.04em;
  text-transform: uppercase;
  color: #011d47;
  background: #fff;

  &::after {
    content: url("../images/upload.svg");
    position: absolute;
    left: 16px;
    top: 50%;
    width: 24px;
    height: 24px;
    transform: translateY(-50%);
  }
}

.wpcf7-response-output {
  display: none;
}

#form-file-info {
  position: absolute;
  left: 211px;
  top: 50%;
  width: fit-content;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: -0.04em;
  text-transform: uppercase;
  color: #011d47;
  transform: translateY(-50%);

  @include m($md) {
    left: 200px;
  }
}

.wpcf7-select {
  margin-bottom: 32px;
  padding-bottom: 16px;
  width: 100%;
  outline: none;
  border-top: none;
  border-right: none;
  border-bottom: 1px solid #767e8b;
  border-left: none;
  font-weight: 600;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: -0.04em;
  color: #011d47;
}

.request {
  .request__contact {
    .career-input-block {
      margin-bottom: 32px;
    }
  }
}

.rates {
  ul:last-of-type {
    li:first-of-type {
      justify-content: flex-end !important;

      p {
        margin-left: 8% !important;
      }
    }
  }
}

// Denys styles

#menu-footer-menu {
  display: block;
}

.dad-pagination {
  padding-top: 88px;
  text-align: center;
}

.dad-pagination a {
  box-sizing: border-box;
  display: inline-block;
  margin-right: 8px;
  margin-left: 8px;
  width: 46px;
  height: 46px;
  vertical-align: top;
  border: 1px solid #abb9ca;
  font-weight: 600;
  font-size: 14px;
  line-height: 46px;
  text-align: center;
  transition: 0.5s;
}

.dad-pagination a:hover {
  color: #fff;
  background-color: #011d47;
}

.dad-pagination span {
  display: inline-block;
  margin-right: 8px;
  margin-left: 8px;
  width: 46px;
  height: 46px;
  vertical-align: top;
  font-weight: 600;
  font-size: 14px;
  line-height: 46px;
  text-align: center;
  color: #fff;
  background-color: #011d47;
}

.dad-pagination .next {
  position: relative;
}

.dad-pagination .next:hover {
  background-color: #abb9ca;
}

.dad-pagination .next::after {
  content: "";
  position: absolute;
  left: 15px;
  top: 15px;
  display: block;
  width: 16px;
  height: 16px;
  background-image: url("https://f2h.celerart.com/wp-content/uploads/2022/10/ArrowRight.svg");
}

.dad-pagination .prev {
  position: relative;
}

.dad-pagination .prev:hover {
  background-color: #abb9ca;
}

.dad-pagination .prev::after {
  content: "";
  position: absolute;
  left: 15px;
  top: 15px;
  display: block;
  width: 16px;
  height: 16px;
  background-image: url(https://f2h.celerart.com/wp-content/uploads/2022/10/ArrowLeft.svg);
}